/* http://meyerweb.com/eric/tools/css/reset/
   v5.0.1 | 20191019
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, menu, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
main, menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, main, menu, nav, section {
  display: block;
}

/* HTML5 hidden-attribute fix for newer browsers */
*[hidden] {
  display: none;
}

body {
  line-height: 1;
}

menu, ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

html {
  font-size: 62.5%;
}

body {
  font-size: 1.6rem;
}

* {
  box-sizing: border-box;
}

body {
  background: white;
}

.wrapper {
  max-width: 40rem;
  margin: auto;
  padding: 3.6rem 2.4rem;
  background: white;
}
@media (min-width: 43.125rem) {
  .wrapper {
    max-width: 69rem;
  }
}

.text {
  color: #151f29;
  font-weight: 300;
  font-size: 1.2rem;
  font-family: Libre Franklin, sans-serif;
  line-height: normal;
}
@media (min-width: 43.125rem) {
  .text {
    font-size: 1.6rem;
  }
}
.text-h1 {
  color: #969696;
  font-size: 2.2rem;
  line-height: 125%;
  letter-spacing: 0.005em;
}
@media (min-width: 43.125rem) {
  .text-h1 {
    font-size: 4.8rem;
  }
}
.text-bold {
  color: #151f29;
  font-weight: 700;
}
.text-secondary {
  color: #969696;
  font-size: 1rem;
  line-height: 120%;
}
@media (min-width: 43.125rem) {
  .text-secondary {
    font-size: 1.2rem;
    line-height: 125%;
  }
}

.icon {
  display: block;
  width: 1.6rem;
  height: 1.6rem;
}

.header {
  margin-top: 4.8rem;
}
.header_logo {
  margin: 0 auto;
}

.logo {
  width: 5.2rem;
  height: 1.6rem;
  background: url(logo.svg) center no-repeat;
  background-size: contain;
}
@media (min-width: 43.125rem) {
  .logo {
    width: 8.45rem;
    height: 2.6rem;
  }
}

.content {
  position: relative;
}
.content_heading {
  margin: 4rem 0 0;
  text-align: center;
}
@media (min-width: 43.125rem) {
  .content_heading {
    margin-top: 5rem;
  }
}
.content_paragraph {
  margin: 1.6rem 0 0;
  text-align: center;
}
@media (min-width: 43.125rem) {
  .content_paragraph {
    font-size: 2rem;
    line-height: 120%;
  }
}
.content_form {
  margin-top: 3.2rem;
}
@media (min-width: 43.125rem) {
  .content_form {
    margin-top: 4rem;
  }
}

.subscribeForm {
  position: absolute;
  top: 100%;
  left: 50%;
  width: calc(100% - 4.8rem);
  max-width: 69rem;
  transform: translateX(-50%);
}
@media (min-width: 43.125rem) {
  .subscribeForm {
    display: flex;
    align-items: flex-start;
    width: 100%;
  }
}
@media (min-width: 43.125rem) {
  .subscribeForm :first-child {
    flex: 1;
  }
}
.subscribeForm_feedback {
  box-sizing: content-box;
  height: 0;
  margin: 0;
  padding: 0 3.2rem;
  overflow: hidden;
  text-align: center;
  border: 0;
  opacity: 0;
  transition: height 0.2s ease-in-out, padding 0.2s ease-in-out, opacity 0.2s ease-in-out;
}
@media (min-width: 43.125rem) {
  .subscribeForm_feedback {
    text-align: left;
  }
}
.subscribeForm_submit {
  width: 100%;
  margin-top: 1rem;
}
@media (min-width: 43.125rem) {
  .subscribeForm_submit {
    width: unset;
    margin-top: 0;
    margin-left: 1.6rem;
  }
}
.subscribeForm-error .emailField_input {
  border-color: #ff5263;
}
.subscribeForm-error .subscribeForm_feedback {
  color: #ff5263;
  font-style: italic;
  letter-spacing: 0.015em;
}
.subscribeForm-success .emailField_input {
  border-color: #2dcd70;
}
.subscribeForm-success .subscribeForm_feedback {
  color: #2dcd70;
}
.subscribeForm-error .subscribeForm_feedback, .subscribeForm-success .subscribeForm_feedback {
  height: 2.4rem;
  padding-top: 0.6rem;
  padding-bottom: 0.2rem;
  opacity: 1;
}
@media (min-width: 43.125rem) {
  .subscribeForm-error .subscribeForm_feedback, .subscribeForm-success .subscribeForm_feedback {
    padding-top: 0.8rem;
  }
}

.emailField {
  display: block;
}
.emailField_input {
  box-sizing: border-box;
  width: 100%;
  height: 4rem;
  padding: 0 3.2rem;
  font: 300 1.2rem Libre Franklin, sans-serif;
  background: white;
  border: 0.1rem solid #c2d3ff;
  border-radius: 2rem;
  transition: border-color 0.2s;
}
@media (min-width: 43.125rem) {
  .emailField_input {
    height: 5.6rem;
    font-size: 1.6rem;
    border-radius: 2.8rem;
  }
}
.emailField_input::placeholder {
  color: #c2d3ff;
}
.emailField_input:focus {
  outline: none;
  box-shadow: 0 0 0 0.3rem #c2d3ff;
}

.srOnly {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(1px, 1px, 1px, 1px);
}

.button {
  box-sizing: border-box;
  height: 4rem;
  padding: 0 3.2rem;
  color: white;
  font-weight: 600;
  font-size: 1.2rem;
  font-family: Libre Franklin, sans-serif;
  background: #4f7df3;
  border: none;
  border-radius: 2rem;
  box-shadow: 0 0.6rem 1rem rgba(79, 125, 243, 0.4);
  transition: 0.2s;
}
@media (min-width: 43.125rem) {
  .button {
    height: 5.6rem;
    padding: 0 6.25rem;
    font-size: 1.6rem;
    border-radius: 2.8rem;
  }
}
.button:focus {
  outline: none;
  box-shadow: 0 0.6rem 1rem rgba(79, 125, 243, 0.4), 0 0 0 0.3rem #c2d3ff;
}
.button:hover {
  background: #7096f5;
}
.button:active {
  background: #2d65f0;
}

.background {
  height: 19.6rem;
  margin-top: 19rem;
  background: url(illustration-dashboard.png) center no-repeat;
  background-size: contain;
}
@media (min-width: 43.125rem) {
  .background {
    height: 39rem;
    margin-top: 18.4rem;
  }
}

.footer {
  margin-top: 12rem;
}
@media (min-width: 43.125rem) {
  .footer {
    margin-top: 7rem;
  }
}
.footer_copyright {
  margin: 2.4rem 0 0;
  text-align: center;
}

.socialLinks {
  display: flex;
  justify-content: center;
  margin: 0;
  padding: 0;
  text-align: center;
  list-style: none;
}
.socialLinks_item {
  display: block;
}
.socialLinks_item:not(:first-child) {
  margin-left: 1.2rem;
}
.socialLinks_link {
  display: flex;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
  width: 3.2rem;
  height: 3.2rem;
  color: #4f7df3;
  border: 0.1rem solid #c2d3ff;
  border-radius: 50%;
  transition: 0.2s;
}
.socialLinks_link:focus {
  border: 0.1rem solid #4f7df3;
  outline: none;
}
.socialLinks_link:hover {
  color: white;
  background: #4f7df3;
  border-color: #4f7df3;
}
.socialLinks_link:active {
  background: #2d65f0;
  border-color: #2d65f0;
}